import "core-js/modules/es.array.for-each";
import "core-js/modules/es.array.index-of";
import "core-js/modules/es.string.link";
import "core-js/modules/web.dom-collections.for-each";
//
//
//
//
//
//
//
//
//
//
export default {
  name: "Help_Center_Left",
  data: function data() {
    return {
      AboutList: [{
        name: "新手必读",
        link: "/Help_Center",
        index: 0
      }, {
        name: "注册及认证",
        link: "/Help_Center/Help_Register_Iden",
        index: 1
      } // { name: "绑定银行卡", link: "/Help_Center/Help_Bind_Bankcard" ,index:2 },
      // { name: "投资操作", link: "/Help_Center/Help_Invest_Operate" ,index:3 },
      // { name: "余额相关", link: "/Help_Center/Help_About_Balance" ,index:4 }
      ],
      selectNav: 0
    };
  },
  created: function created() {},
  mounted: function mounted() {
    this.activeChange();
  },
  watch: {
    // 监听路由的变化，来显示文档标题和导航栏高亮
    $route: function $route(to, from) {
      this.activeChange();
    }
  },
  methods: {
    aboutNav: function aboutNav(item, index) {
      if (this.selectNav == index) {
        return;
      }

      this.$router.push({
        path: item.link
      });
    },
    activeChange: function activeChange() {
      var _this = this;

      var routeChoice = this.$route.path;
      this.AboutList.forEach(function (item, i) {
        if (routeChoice.indexOf(item.link) > -1) {
          _this.selectNav = item.index;
        }
      });
    }
  }
};